
import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import 'element-ui/lib/theme-default/index.css'

export default {
components: {
XHeader, XInput, XButton, Group, Cell, Toast
},
data() {
return {
phoneValue: 0,
showWarn: false,
showWarnTxt: '',
routeName: this.$route.name,
times: 60,
getCodeBtn: false,
restaurants: [
{ "value": "深圳中学", "sId": "1" },
{ "value": "深圳中学1", "sId": "2" },
{ "value": "深圳中学2", "sId": "3" },
{ "value": "深圳中学3", "sId": "4" },
{ "value": "深圳中学4", "sId": "5" },
{ "value": "深圳中学5", "sId": "6" },
{ "value": "深圳中学6", "sId": "7" },
],
state2: '',
}
},

methods: {
getCode() {
console.log(this.phoneValue)
let value = this.phoneValue
if (!value && value == 0) {
this.showWarnTxt = '电话不能为空'
this.showWarn = true
} else if (!(/^1(3|4|5|7|8)\d{9}$/.test(value))) {
this.showWarnTxt = '电话格式有误'
this.showWarn = true
} else {
console.log('ok')
let me = this;
me.getCodeBtn = true;
let interval = window.setInterval(function () {
if ((me.times--) <= 0) {
me.times = 60;
me.getCodeBtn = false;
window.clearInterval(interval);
}
}, 1000);
this.$axios({
method: 'POST',
url: 'http://192.168.0.19/hxjy/weixin/massage',
data: {
phone:this.phoneValue
},
}).then((r) => {
console.log(r)
}).catch((err) => {
console.log(err);
})
}
},
submit() {
console.log(this.searchVal)
console.log(this.resultVal)
},
querySearch(queryString, cb) {
var restaurants = this.restaurants;
var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;

cb(results);
},
createFilter(queryString) {
return (restaurant) => {
return (restaurant.value.indexOf(queryString.toLowerCase()) === 0);
}
},
handleSelect(item) {
console.log(item);
},

},
created() {
document.title = this.$route.name
}
}



